@use '../../../styles/mixins';

.profilewrapper {
    display: flex;
    align-items: flex-start;
    gap: 16px;
}

.avatarframe {
    display: flex;
    flex-direction: column;
    gap: 4px;
    align-items: center;
}

.avatar {
    width: 72px;
    border: 1px solid grey;
    border-radius: 50%;
    text-align: center;
    vertical-align: middle;

    @include mixins.mq(tablet) {
        width: 144px;
        height: 144px;
    }
}

.photo {
    border-radius: 50%;

    @include mixins.mq(tablet) {
        width: 144px;
    }
}

.userframe {
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.profilebox {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.username {
    font-size: 18px;
    font-weight: 500;
    color: var(--text-color);
}

.ratingbox {
    display: flex;
    align-items: center;
    gap: 8px;
}

.iconlist {
    display: flex;
    gap: 2px;
}

.iconEmpty {
    fill: var(--empty-color);
}

.iconFilled {
    fill: var(--accent-color);
}

.valuation {
    display: flex;
    font-size: 14px;
    font-weight: 500;
    color: var(--empty-color);
}

.leftvalue {
    margin-right: 4px;
}

.rightvalue {
    margin-left: 4px;
}

.text {
    font-size: 14px;
    color: var(--text-color)
}
.infowrapper {
    display: flex;
}

.profileinfo {
    display: flex;
    flex-direction: column;
    gap: 12px;
    color: var(--text-color);

    @include mixins.mq(mobile) {
        flex-direction: row;
        flex-wrap: wrap;
    }
}

.iconBefore {
    margin-right: 8px;
    width: 16px;
    height: 16px;
}